// /components/PostList.tsx
import { useState } from 'react';
import PostCard, { Post } from './PostCard';
import styles from '../styles/PostList.module.css';

const mockPosts: Post[] = [
  { title: '第一篇文章', date: '2026-01-26', excerpt: '这是第一篇文章的内容预览...' },
  { title: '第二篇文章', date: '2026-01-25', excerpt: '第二篇文章内容的一部分...' },
];

export default function PostList() {
  const [view, setView] = useState<'card' | 'list'>('card');

  return (
    <section>
      <div className={styles.toggle}>
        <button onClick={() => setView('card')}>卡片</button>
        <button onClick={() => setView('list')}>列表</button>
      </div>
      <div className={view === 'card' ? styles.cardGrid : styles.listGrid}>
        {mockPosts.map((post, idx) => (
          <PostCard key={idx} post={post} />
        ))}
      </div>
    </section>
  );
}